<template>
    <!--  -->
    <fieldset id="mobileApp">
        <legend>:</legend>
        <div>
            
        </div>
        <div class="form-warn">
            <br>
            1. testrun_mobileapk<br>
            2. 2testrun_mobile<br>
            3. apkT-DASH<br>
        </div>
        <div class="form-area">
            <label for="appName"></label><br>
            <input type="text" class="input-text" v-model="inputedAppName" name="appName" @input="updateOpt"
                placeholder=""><br>
            <label for="appPath">
                apk
                <PathHelp />
            </label><br>
            <input type="text" class="input-text" v-model="inputedAppPath" name="appPath" @input="updateOpt"
                placeholder="/Path/to/App.apk"><br>
        </div>
    </fieldset>
</template>

<script>
import { onMounted, ref } from 'vue';

import strUtils from '@/js/strUtils';
import cacheUtils from '@/js/cacheUtils';

export default {
    name: 'MobileAppForm',
    setup(_, { emit }) {
        // ###############
        //  #
        // ###############
        const inputedAppName = ref("")
        const inputedAppPath = ref("")

        // #############
        //  #
        // #############
        const updateOpt = async () => {
            let createdOpt = ""

            // 
            if (inputedAppName.value) {
                createdOpt += ` --mobile_app_name ${inputedAppName.value}`
            }
            if (inputedAppPath.value) {
                inputedAppPath.value = await strUtils.replaceSlush(inputedAppPath.value)
                createdOpt += ` --mobile_app_path ${inputedAppPath.value}`
                createdOpt += ` --mobile_app_kind apk_file`
            }

            // cache
            await cacheUtils.saveData("mobileAppName", inputedAppName.value)
            await cacheUtils.saveData("mobileAppPath", inputedAppPath.value)

            // &
            emit("update-opt", "mobileApp", createdOpt)
        }

        // ###############
        //  #
        // ###############
        onMounted(async () => {
            // cache
            inputedAppName.value = await cacheUtils.getData("mobileAppName")
            inputedAppPath.value = await cacheUtils.getData("mobileAppPath")
            await updateOpt()
        })

        return {
            // 
            inputedAppName,
            inputedAppPath,
            // 
            updateOpt,
        }
    }
}
</script>
